<template>
  <v-container fluid>
    <!-- <h1>{{ msg }}</h1>-->
    <ul>
      <li><router-link :to="'/'">Home</router-link></li>
    </ul>

    <h2 class="mb-8 display-1 text-xs-center">Сертификация</h2>

    <v-card>
      <v-card-text>
        <v-container v-if="items[index]" fluid>
          <v-layout row wrap>
            <v-flex xs3>
              <v-subheader v-text="'Вопрос'"></v-subheader>
            </v-flex>
            <v-flex xs9>
              <h1 class="question">{{ items[index].question }}</h1>
            </v-flex>
          </v-layout>

          <v-layout row wrap>
            <v-flex xs3>
              <v-subheader v-text="'Варианты'"></v-subheader>
            </v-flex>
            <v-flex xs9>
              <v-radio-group v-model="answer" :mandatory="false">
                <v-layout
                  v-for="(variant, idx) in items[index].answers"
                  :key="idx"
                  row
                  wrap
                  class="mb-3"
                >
                  <v-flex xs1>
                    <v-radio :value="idx" class="mt-1" wrap></v-radio>
                  </v-flex>
                  <v-flex xs11>
                    <p class="answer mb-3" v-text="variant"></p>
                  </v-flex>
                </v-layout>
              </v-radio-group>
            </v-flex>
          </v-layout>
          {{ this.items[this.index].correct }}
        </v-container>
      </v-card-text>
      <v-card-actions row wrap>
        <v-spacer></v-spacer>
        <v-btn text color="orange" :disabled="!answerselected" @click.stop="apply"
          >Принять ответ</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: "hello",
  beforeMount() {
    this.selected = JSON.parse(localStorage.getItem("selected"));
    if (this.selected != null) {
      // fetch("/static/data.json")
      fetch("http://gateway.ipfs.io/ipns/k2k4r8ly6t6jmfevyjtdmaw9p1cfew0ehr72k0idqd0hbstl4bgnc50i")
        .then((body) => body.json())
        .then((data) => {
          this.items = [];
          this.selected.config.forEach((element) => {
            for (let cnt = 0; cnt < element.count; cnt++) {
              this.collect(data, this.selected.code, element.subcat);
            }
            window.console.log("Next correct", this.items[this.index].correct);
          });
        });
    }
  },
  methods: {
    collect(data, att, subcat) {
      const shuffled = data.sort(() => (Math.random() > 0.5 ? 1 : -1));
      for (let idx = 0, len = shuffled.length; idx < len; idx++) {
        const el = shuffled[idx];
        if (el.att === att && el.subcat === subcat) {
          this.items.push(el);
          return;
        }
      }
    },
    apply() {
      if (this.index < 10) {
        if (this.answer + 1 === this.items[this.index].correct) {
          window.console.log("correct answer");
        } else {
          window.console.log("wrong answer");
        }

        this.answer = null;
        this.$nextTick(() => {
          this.index++;
          if (this.index === 10) {
            const data = JSON.stringify(this.items);
            window.console.log(data);
            this.$router.push("/complete");
          }
        });
      }
    },
  },
  computed: {
    answerselected() {
      return this.answer != null;
    },
  },
  data() {
    return {
      msg: "Welcome to Your Vue.js PWA",
      items: [],
      selected: null,
      index: 0,
      answer: null,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495e;
}
.answer {
  font-size: 18px;
}

.question {
  font-size: 22px;
}
</style>



