import Vue from 'vue';
import vuetify from '@/plugins/vuetify'; // path to vuetify export

import App from './App';
import router from './router';

// Vue.config.productionTip = false;


new Vue({
  vuetify,
  router,
  template: '<App/>',
  components: { App },
}).$mount('#app');



